import { PurchasingPurchaseOrderItemService } from 'src/app/sdk/purchasing/services/purchasing-purchase-order-item.service';
import { Purchase_order, Purchase_order_item } from 'src/app/sdk/purchasing/models';
import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseOrderService, } from 'src/app/sdk/purchasing/services';
import { VendorVendorService } from 'src/app/sdk/asset/services';
import { Vendor } from 'src/app/sdk/asset/models';
import { CompanyKantorService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Kantor } from 'src/app/sdk/hr/models';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private purchasingPurchaseOrderService: PurchasingPurchaseOrderService,
        private purchasingPurchaseOrderItemService: PurchasingPurchaseOrderItemService,
        private vendorVendorService: VendorVendorService,
        private companyKantorService: CompanyKantorService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }

    dataPO: Purchase_order = {}
    list_item: Purchase_order_item[] = []
    id_purchase_order: string = ''

    dataKantor: Kantor = {}
    dataVendor: Vendor = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_purchase_order'))
                if (params.get('id_purchase_order')) {
                    this.id_purchase_order = params.get('id_purchase_order')
                }

                this.getDetailRequest()
                this.getDetailItem()
                this.getLog()

            }

        )


        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailRequest()
                    this.getDetailItem()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''

    getDetailRequest() {
        this.spinner.show()
        this.purchasingPurchaseOrderService.getPurchasingPurchaseOrdersId(this.id_purchase_order).subscribe(
            data => {
                this.dataPO = data.data;
                this.getkantor(data.data.id_kantor_pengiriman);
                this.getVendor(data.data.id_vendor);
                this.spinner.hide()

                this.params = {
                    id: data.data.id_purchase_order,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_purchase_order"
                }
                this.standarApprovalService.param.next(this.params)
            }
        )
    }

    getkantor(id_kantor_request) {
        this.companyKantorService.getCompanyKantorsId(id_kantor_request).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }

    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data;
            }
        )
    }

    getDetailItem() {
        this.purchasingPurchaseOrderItemService.getPurchasingPurchaseOrderItems(JSON.stringify({ id_purchase_order: this.id_purchase_order })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_purchase_order })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/purchasing/po/order//list'])
    }

}
