import { Component, OnInit, Input } from '@angular/core';
import { PurchasingPurchaseRequestItemService } from 'src/app/sdk/purchasing/services';
import { NzDrawerRef } from 'ng-zorro-antd';

@Component({
  selector: 'app-search-request',
  templateUrl: './search-request.component.html',
  styleUrls: ['./search-request.component.scss']
})
export class SearchRequestComponent implements OnInit {

    @Input() value = '';

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private purchasingPurchaseRequestItemService: PurchasingPurchaseRequestItemService
    ) { }

    ngOnInit() {
        this.searchData()
    }

    close(): void {
        this.drawerRef.close(this.value);
    }

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData: any[] = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.purchasingPurchaseRequestItemService
            .getPurchasingPurchaseRequestItems(
                JSON.stringify({
                    pageIndex: this.pageIndex,
                    pageSize: this.pageSize,
                    sortKey: this.sortKey,
                    sortValue: this.sortValue,
                    search: this.search,
                }),
            )
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data

            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }

    selectPRItem(id_purchase_request_item: string)
    {
        this.value  = id_purchase_request_item;
        this.close() ;
    }

}
