import { PesanService } from 'src/app/services/tool/pesan.service';
import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseOrderService } from 'src/app/sdk/purchasing/services/purchasing-purchase-order.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private purchasingPurchaseOrderService: PurchasingPurchaseOrderService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
    ) { }

    ngOnInit() {
        this.searchData()
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.purchasingPurchaseOrderService
            .getPurchasingPurchaseOrders(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }




    delete(id) {
        this.spinner.show();
        this.purchasingPurchaseOrderService.deletePurchasingPurchaseOrdersId(id).subscribe(
            data => {
                this.searchData();
            },
            err => {
                this.pesan.pesanWarningForm('Data tidak boleh di delete')
                this.spinner.hide()
            },
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
