import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseOrderService, PurchasingPurchaseOrderItemService, PurchasingPurchaseRequestItemService } from 'src/app/sdk/purchasing/services';
import { CompanyKantorService } from 'src/app/sdk/hr/services';
import { AssetModelxService, VendorVendorService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Purchase_order, Purchase_order_item } from 'src/app/sdk/purchasing/models';
import { Kantor } from 'src/app/sdk/hr/models';
import { FormControl } from '@angular/forms';
import { SearchRequestComponent } from '../search-request/search-request.component';
import { Modelx, Vendor } from 'src/app/sdk/asset/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private purchasingPurchaseOrderService: PurchasingPurchaseOrderService,
        private purchasingPurchaseOrderItemService: PurchasingPurchaseOrderItemService,
        private purchasingPurchaseRequestItemService: PurchasingPurchaseRequestItemService,
        private vendorVendorService: VendorVendorService,
        private companyKantorService: CompanyKantorService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService
    ) { }

    dataPO: Purchase_order = {}
    list_item: Purchase_order_item[] = []
    id_purchase_order: string = ''

    listVendor: Vendor[] = []
    listKantor: Kantor[] = []

    ngOnInit() {
        this.id_purchase_order = this.activatedRoute.snapshot.paramMap.get('id_purchase_order')
        this.getPreeData()
        this.form()
        this.getData()
        this.getListItem()

    }
    getPreeData() {
        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })

        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })

    }


    getData() {
        this.purchasingPurchaseOrderService.getPurchasingPurchaseOrdersId(this.id_purchase_order).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }


    dataForm: FormGroupTypeSafe<Purchase_order>
    form() {
        this.dataForm = this.fb.group<Purchase_order>({
            id_purchase_order: new FormControl(''),
            id_vendor: new FormControl(''),
            id_kantor_pengiriman: new FormControl(''),
            tanggal_penawaran: new FormControl(''),
            id_dokumen: new FormControl(''),
            tanggal_pengiriman: new FormControl(''),
            tanggal_po: new FormControl(''),
            no_penawaran: new FormControl(''),
            nomor: new FormControl(''),
            posisi: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_data_flow: new FormControl(''),
            status: new FormControl(''),
            total_nilai: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl('')
        })
    }

    // ## LIST ITEM -----------------------------------
    id_purchase_request_item: string = null;
    add_item() {
        const drawerRef = this.drawerService.create<SearchRequestComponent, { value: string }, string>({
            nzTitle: 'Search Model',
            nzContent: SearchRequestComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_purchase_request_item = data;
                this.beforeRequest()
            }
        });
    }


    getListItem() {
        this.purchasingPurchaseOrderItemService.getPurchasingPurchaseOrderItems(JSON.stringify({ id_purchase_order: this.id_purchase_order })).subscribe(
            data => {
                this.list_item = data.data
            }
        )
    }

    deleteItem(id) {
        this.spinner.show()
        this.purchasingPurchaseOrderItemService.deletePurchasingPurchaseOrderItemsId(id).subscribe(
            data => {
                this.getListItem()
                this.spinner.hide()
            }
        )
    }


    dataItem: any = {};

    beforeRequest() {
        console.log("panggie ko")
        this.purchasingPurchaseRequestItemService.getPurchasingPurchaseRequestItemsId(this.id_purchase_request_item).subscribe(
            data => {
                this.dataItem.qty = data.data.qty;
                this.dataItem.id_purchase_request_item = data.data.id_purchase_request_item;
                this.dataItem.id_model = data.data.id_model;
                this.getModel(this.dataItem.id_model)
                console.log(this.dataItem)
            }
        )
    }

    dataModel: Modelx = {};
    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.dataModel.keyword = data.data.keyword;
                this.dataModel.part_number = data.data.part_number;
            }
        )
    }

    harga_satuan: string = ''
    simpan_item() {
        let param: Purchase_order_item = {
            id_purchase_request_item: this.dataItem.id_purchase_request_item,
            id_purchase_order: this.id_purchase_order,
            harga_satuan: this.harga_satuan
        };

        this.spinner.show()
        //simpan ka server
        this.purchasingPurchaseOrderItemService.postPurchasingPurchaseOrderItems(param).subscribe(
            data => {
                this.id_purchase_order = null;
                this.spinner.hide();
                this.getListItem()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

    // ## END  LIST ITEM -----------------------------------



    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        if (this.list_item.length < 1) {
            this.pesan.pesanWarningForm('Anda harus pilih Item Pembelian!')
            this.spinner.hide()
            return
        }

        this.dataPO = this.dataForm.value
        this.purchasingPurchaseOrderService.putPurchasingPurchaseOrdersId({ id: this.id_purchase_order, body: this.dataPO }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/purchasing/po/order/list'])
    }

}
